import React, {Fragment} from "react"
import { graphql } from "gatsby"
import Img from 'gatsby-image'

import './post.css'

export default function Post ({ data }) {
  const { markdownRemark } = data
  const { frontmatter, html } = markdownRemark
  console.log(frontmatter)
  const languageClass = frontmatter.lang === 'ar'? 'rightToLeft': null
  let handleBack = ()=>{
    window.location = '/blog'
  }
  return (  
    <>
    <nav className={languageClass}><a onClick={()=>{handleBack()}}>{languageClass === 'rightToLeft'? 'العودة':'Home'}</a></nav>
      <div style={{ width: '700px', margin: "4rem auto" }}>
        <h1 className={`article-title ${languageClass}`}>{frontmatter.title}</h1>
        <h5 className={languageClass}>{frontmatter.author} &#8206;@Nabil_Tharwat16<br/><p className={languageClass}>
        {
          languageClass === 'rightToLeft'? `${Math.floor(parseInt(frontmatter.length)/350)} دقائق من القراءة`: `${frontmatter.date} - ${Math.floor(parseInt(frontmatter.length)/350)} min read` 
        }
        </p></h5>
        <Img fluid={frontmatter.image.childImageSharp.fluid}/>
        <div
          className={`blog-post-content ${languageClass}`}
          dangerouslySetInnerHTML={{ __html: html }}
        />
    </div>
  </>
)}

export const pageQuery = graphql`
  query($path: String!) {
    markdownRemark(frontmatter: { 
      path: { eq: $path }
    }) {
      html
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        path
        title
        description
        author
        length
        lang
        image {
          childImageSharp {
            fluid(maxWidth: 786) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    }
  }
`